@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,600,700|Roboto+Slab:700|Roboto:400,500,700|Montserrat:400,500,600,700,800&subset=latin-ext);
html, body {
  font-family: "Montserrat", sans-serif;
  font-size: 16px; }

h1, h2, h3, h4, h5, h6 {
  font-family: "Montserrat", sans-serif; }

h1 {
  font-size: 42px;
  font-weight: 600;
  text-transform: uppercase; }

h3 {
  font-size: 24px;
  font-weight: 800;
  text-transform: uppercase;
  margin-top: 40px; }

p, ul, ol, li {
  text-align: center;
  font-family: "Montserrat", sans-serif; }

hr {
  border: 1px solid #8E6FCE; }

a {
  text-decoration: none;
  color: #8E6FCE;
  font-weight: 700; }

a:hover {
  color: #d9ceee;
  text-decoration: none; }

ul {
  list-style-type: square; }

label {
  font-weight: normal;
  margin-left: "300px";
  padding: "50px";
  color: red; }

.indent {
  margin-left: "100px"; }

.nav-link {
  color: #8E6FCE !important;
  font-weight: bold; }
  .nav-link:hover {
    color: #d9ceee !important; }

.navbar-brand {
  color: #8E6FCE !important;
  font-weight: 400; }

.main {
  padding: 0 20px; }

.main-info-container {
  text-align: left; }

.main-info-container p {
  text-align: justify !important; }

.text-center {
  text-align: center; }

#checklist {
  text-align: left; }

#footer-actions {
  padding-top: 40px;
  padding-bottom: 30px;
  background-color: #262626;
  color: white;
  font-size: 16px;
  font-weight: normal; }

#footer-actions .container {
  padding: 0;
  margin: 0 auto; }

#footer-actions .container div {
  padding: 0; }

#footer-actions h1 {
  font-size: 24px;
  font-weight: 800;
  text-transform: uppercase;
  margin-bottom: 20px; }

#footer-actions p {
  margin-bottom: 20px; }

#footer-actions .button {
  display: block;
  background-color: #8E6FCE !important;
  color: white;
  width: 190px;
  height: 50px;
  line-height: 50px;
  font-size: 15px;
  margin: auto; }

#footer-actions .button:hover {
  background-color: #ac95db; }

#footer {
  background-color: #262626;
  color: white;
  padding: 10px 10px;
  font-size: 16px;
  box-shadow: none;
  border-top: solid 1px #3c3c3c; }

#footer a:link, #footer a:hover, #footer a:active, #footer a:visited {
  color: white;
  font-weight: 400;
  text-decoration: none; }

